    <div
      ref="progress"
      class="audio-controls__progress-outer"
      @click.self="onClick"
    >
      <div
        class="audio-controls__progress-inner"
        :style="{ width: percent + '%' }"
        @click.self="onClick"
      />
      <div
        class="audio-controls__progress-point"
        :style="{ left: percent + '%' }"
        v-drag:callback="onDrag"
      />
    </div>
  </div>
</template>

<script>
import directives from "./../directives";
export default {
  name: "AudioProgress",
  props: {
    percent: {
      type: Number,
      default: null,
    },
  },
  data() {
    return {
      selfPercent: this.percent ? this.percent : 0,
    };
  },
  methods: {
    onClick: function(e) {
      this.selfPercent = (e.offsetX / this.$refs.progress.offsetWidth) * 100;
      this.$emit("change", this.selfPercent);
    },
    onDrag: function(percent) {
      this.selfPercent = percent;
      this.$emit("change", this.selfPercent);
    },
  },
  directives,
};
</script>
